.sensor_view{
    width: 100%;
    border-collapse: collapse;
    border:1px solid #000;
    text-align: center;
    font-size: 8px;
}

.sensor_view tr, .sensor_view td{
    border:1px solid #000;
}

.realtime_loading{
    position: fixed;
    left:0;
    right:0;
    top:45%;
    bottom:0;
}


